import React, { Component } from "react";
import Cell from "./Cell";


export default class Board extends Component{
  constructor(props)
  {
    super(props);
    //console.log("Board props:" + props);
    this.state = {
        boardSize: props.boardSize
    };
  }

  renderSquares()
  {
    //console.log("board debug grid" + this.props.grid);
    //console.log("board debug start grid" + this.props.startGrid);

    const cells = [];
    const trueSize = this.props.boardSize * this.props.boardSize;
    //console.log("Debug Board.trueSize" + trueSize)
    for (let i = 0; i < trueSize; i++) {
      for (let j = 0; j < trueSize; j++) {
        const index = `${i},${j}`;
        
        cells.push(
          <Cell
            className="gridlineHorizon"
            style={
              this.props.startGrid[i][j] === 0 ? { color: "red" } : { color: "black" }
              

              //{color: "black"}
            }
            key={index}
            id={index}
            onChange={this.props.onChange}
            value={this.props.grid[i][j] === 0 ? "" : this.props.grid[i][j]}
            //value = {1}
            disabled={this.props.disabled}
          />
        );
      }
    }

    return cells;
  }

  render()
  {
    let classname = "sudoku-grid";
    classname+= this.props.boardSize.toString();

    return  (
      <div
      className={classname}
      >
        

        {this.renderSquares()}
      </div>
    )
  }

}