import React, { Component, useEffect, useState } from "react";
import { Row, Col, Button, FormCheck, Card } from "react-bootstrap";
import Board from "./Board";
import sudokuService, {
  EMPTY_GRID,
  EMPTY_START_GRID,
} from "../Services/sudokuService";
import storageService from "../Services/storageService";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Slider from "@mui/material/Slider";
import { BsFillGrid3X3GapFill, BsSpeedometer } from "react-icons/bs";

export default class SudokuSolver extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isGridDisabled: false,
      grid: EMPTY_GRID(9),
      startGrid: EMPTY_START_GRID(9),

      isShowProcessChecked: true,
      isSolved: false,
      isSolving: false,
      progressSpeed: 10,
      sliderValue: 0,
      boardSize: 3,

    };

    //required so taht funcitons can handle changing states
    this.handleSolveButtonClicked = this.handleSolveButtonClicked.bind(this);
    this.handleSpeedSliderChange = this.handleSpeedSliderChange.bind(this);
    this.handleSizeSliderChange = this.handleSizeSliderChange.bind(this);
    this.handleValueChange = this.handleValueChange.bind(this);
    this.reset = this.reset.bind(this);
    this.undo = this.undo.bind(this);
    
  }

  componentDidMount() {
    const storageBoard = storageService.getBoard();
    //if (storageBoard) this.setState({grid: storageBoard});
  }

  handleSpeedSliderChange(e, newValue) {
    this.setState({
      progressSpeed: newValue,
    });
  }

  handleSizeSliderChange(e, newValue) {
    console.log("Slider size changed" + newValue);
    if(this.state.isSolving) return;
    else {
      this.setState({
        boardSize: newValue,
        grid: EMPTY_GRID(newValue*newValue),
        startGrid: EMPTY_START_GRID(newValue*newValue),
        isGridDisabled: false

      });

      this.forceUpdate();
    }
    
  }

  handleValueChange(e, id) {
    const { value } = e.target;
    if ((value <= 16 && value > 0) || value === "") {
      const position = id.split(",");
      const newGrid = this.state.grid.map((arr) => arr.slice());
      if (value === "") newGrid[position[0]][position[1]] = 0;
      else newGrid[position[0]][position[1]] = Number(value);
      this.setState({ grid: newGrid });
      storageService.setBoard(newGrid);
    }
  }

  showProgress = async (progress) => {
    this.setState({ isGridDisabled: true });
    for (const gridi of progress) {
      this.setState({ grid: gridi });
      await new Promise((resolve) =>
        setTimeout(resolve, this.state.progressSpeed)
      );
    }
    this.setState({ isSolved: true, isSolving: false });
  };

  handleSolveButtonClicked() {
    this.setState({
      isSolving: true,
      isSolved: false,
      startGrid: this.state.grid.map((arr) => arr.slice()),
    });
    const progress = sudokuService.solve(this.state.grid, this.state.boardSize);
    if (this.state.isShowProcessChecked) {
      this.showProgress(progress);
    } else {
      this.setState({
        isGridDisabled: true,
        grid: progress[progress.length - 1],
        isSolved: true,
        isSolving: false,
      });
    }
  }

  reset() {
    this.setState({
      grid: EMPTY_GRID(this.state.boardSize*this.state.boardSize),
      startGrid: EMPTY_START_GRID(this.state.boardSize*this.state.boardSize),
      isGridDisabled: false,
      isSolved: false,
    });

    storageService.setBoard(EMPTY_GRID());
  }

  undo() {
    this.setState({
      isGridDisabled: false,
      grid: this.state.startGrid,
      startGrid: EMPTY_START_GRID(this.state.boardSize * this.state.boardSize),
      isSolved: false,
    });
  }

  render() {
    console.log("Sudoku solver rendered");
    return (
      <Row className="mt-4">
        <Col sm={8} className="mb-5">
          <Board
            startGrid={this.state.startGrid}
            grid={this.state.grid}
            onChange={this.handleValueChange}
            disabled={this.state.isGridDisabled}
            boardSize={this.state.boardSize}
          ></Board>
        </Col>
        <Col lg className="mb-5">
          <Card className="shadow">
            <Card.Body>
              <Button
                variant="dark"
                disabled={this.state.isSolving}
                onClick={() => {
                  this.reset();
                  const randomGrid = sudokuService.getRandomExample(this.state.boardSize);
                  this.setState({ grid: randomGrid , startGrid: EMPTY_START_GRID(this.state.boardSize*this.state.boardSize)});
                  storageService.setBoard(randomGrid);
                }}
              >
                Random Board
              </Button>


              <Box className="slider">
                <Typography id="input-slider" gutterBottom align="left">
                  Speed
                </Typography>
                <Grid container spacing={2} alignItems="left">
                  <Grid item>
                    <BsSpeedometer />
                  </Grid>
                  <Grid item xs>
                    <Slider
                      value={
                        this.state.progressSpeed
                      }
                      onChange={this.handleSpeedSliderChange}
                      aria-label="Temperature"
                      step={1}
                      valueLabelDisplay="auto"
                      min={1}
                      max={1000}
                    />
                  </Grid>
                </Grid>
              </Box>

              <Box className="slider">
                <Typography id="input-slider" gutterBottom align="left">
                  Grid Size
                </Typography>
                <Grid container spacing={2} alignItems="left">
                  <Grid item>
                    <BsFillGrid3X3GapFill />
                  </Grid>
                  <Grid item xs>
                    <Slider
                      value={
                        this.state.boardSize
                      }
                      onChange={this.handleSizeSliderChange}
                      aria-label="Temperature"
                      step={1}
                      marks
                      valueLabelDisplay="auto"
                      min={2}
                      max={5}
                    />
                  </Grid>
                </Grid>
              </Box>

              <br />
              {this.state.isSolving ? (
                <Button
                  id="btn"
                  className="mt-3"
                  variant="dark"
                  onClick={() => window.location.reload()}
                >
                  Stop
                </Button>
              ) : (
                <Button
                  id="btn"
                  className="mt-3"
                  variant="dark"
                  onClick={this.handleSolveButtonClicked}
                >
                  Solve
                </Button>
              )}
              <FormCheck
                className="mt-3"
                type="checkbox"
                label="Show solving process"
                disabled={this.state.isSolving}
                checked={this.state.isShowProcessChecked}
                onChange={(e) =>
                  this.setState({ isShowProcessChecked: e.target.checked })
                }
              />
              {this.state.isShowProcessChecked ? (
                <p className="mt-3 text-danger">
                  Animation might take long time :(
                </p>
              ) : (
                ""
              )}
              <Button
                id="btn"
                className="mt-3 mr-1"
                variant="dark"
                disabled={this.state.isSolving}
                onClick={this.reset}
              >
                Clear
              </Button>
              <Button
                id="btn"
                className="mt-3 ml-1"
                variant="dark"
                disabled={!this.state.isSolved}
                onClick={this.undo}
              >
                <i className="fas fa-undo" />
              </Button>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    );
  }
}
